"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import Link from "next/link";
import {
  Minus,
  Plus,
  Heart,
  Truck,
  ChevronLeft,
  ChevronRight,
} from "lucide-react";

import { useCart } from "@/components/context/CartContext";
import { useWishlist } from "@/components/context/WishlistContext";
import AuthModal from "@/components/includes/AuthModal";
import BuyNowModal from "@/components/includes/BuyNowModal";

interface Product {
  id: number;
  slug: string;
  title: string;
  price: string;
  mrp?: string;
  image1?: string;
  image2?: string;
  image3?: string;
  image4?: string;
  available_sizes?: string[];
  stock_qty?: number;
  is_available: boolean;
  delivery_charge?: number;
  age_category: string;
  product_category: string;
  material_type?: string;
  fit_type?: string;
  pattern_design?: string;
  age_limits?: string;
}

const AGE_CATEGORY_LABELS: Record<string, string> = {
  kids_boy: "Kids (Boy)",
  kids_girl: "Kids (Girl)",
  kids_unisex: "Kids (Unisex)",
  adults_men: "Adults (Men)",
  adults_women: "Adults (Women)",
  adults_unisex: "Adults (Unisex)",
  all_age_men: "All Age (Men)",
  all_age_women: "All Age (Women)",
  all_age_unisex: "All Age (Unisex)",
};

export default function ProductDetailPage() {
  const { slug } = useParams();
  const router = useRouter();

  const [product, setProduct] = useState<Product | null>(null);
  const [relatedProducts, setRelatedProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [currentImage, setCurrentImage] = useState(0);
  const [selectedSize, setSelectedSize] = useState("");
  const [quantity, setQuantity] = useState(1);
  const [showAuth, setShowAuth] = useState(false);
  const [showBuyNow, setShowBuyNow] = useState(false);
  const [wishlistLoading, setWishlistLoading] = useState(false);

  const { addToCart, token, cartItems } = useCart();
  const { wishlistItems, addToWishlist } = useWishlist();

  const inWishlist = wishlistItems.some((i: any) => i.slug === slug);
  const inCart = cartItems.some((i: any) => i.slug === slug);

  /* ---------------- Fetch product ---------------- */
  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const res = await fetch(
          `${process.env.NEXT_PUBLIC_API_BASE_URL}/public/products/${slug}/`
        );
        if (!res.ok) throw new Error("Product not found");
        const data = await res.json();
        setProduct(data);
        if (data.available_sizes?.length) {
          setSelectedSize(data.available_sizes[0]);
        }
      } catch {
        setError("Product not found");
      } finally {
        setLoading(false);
      }
    };

    fetchProduct();
  }, [slug]);

  /* ---------------- Fetch related products ---------------- */
  useEffect(() => {
    if (!product) return;

    const fetchRelated = async () => {
      try {
        const res = await fetch(
          `${process.env.NEXT_PUBLIC_API_BASE_URL}/public/products/`
        );
        if (!res.ok) return;

        const data: Product[] = await res.json();
        const related = data
          .filter(
            (p) =>
              p.slug !== product.slug &&
              p.product_category === product.product_category &&
              p.age_category === product.age_category
          )
          .slice(0, 6);

        setRelatedProducts(related);
      } catch (err) {
        console.error("Related products error", err);
      }
    };

    fetchRelated();
  }, [product]);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        Loading product...
      </div>
    );
  }

  if (error || !product) {
    return (
      <div className="min-h-screen flex items-center justify-center text-red-500">
        {error}
      </div>
    );
  }

  const images = [
    product.image1,
    product.image2,
    product.image3,
    product.image4,
  ].filter(Boolean) as string[];

  const discountPercent =
    product.mrp && parseFloat(product.mrp) > parseFloat(product.price)
      ? Math.round(
          ((+product.mrp - +product.price) / +product.mrp) * 100
        )
      : 0;

  const getEstimatedDelivery = () => {
    const s = new Date();
    const e = new Date();
    s.setDate(s.getDate() + 2);
    e.setDate(e.getDate() + 5);
    return `${s.toDateString().slice(4, 10)} - ${e
      .toDateString()
      .slice(4, 10)}`;
  };

  /* ---------------- UI ---------------- */
  return (
    <div className="bg-gray-50 min-h-screen">
      {/* Product Section */}
      <div className="max-w-7xl mx-auto p-6 grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Images */}
        <div>
          <div className="relative bg-white rounded-lg aspect-square">
            <img
              src={images[currentImage]}
              className="w-full h-full object-contain"
              alt={product.title}
            />
            {images.length > 1 && (
              <>
                <button
                  onClick={() =>
                    setCurrentImage(
                      (currentImage - 1 + images.length) % images.length
                    )
                  }
                  className="absolute left-2 top-1/2 bg-white p-2 rounded-full"
                >
                  <ChevronLeft />
                </button>
                <button
                  onClick={() =>
                    setCurrentImage((currentImage + 1) % images.length)
                  }
                  className="absolute right-2 top-1/2 bg-white p-2 rounded-full"
                >
                  <ChevronRight />
                </button>
              </>
            )}
            {discountPercent > 0 && (
              <span className="absolute top-4 right-4 bg-red-500 text-white px-3 py-1 rounded-full">
                {discountPercent}% OFF
              </span>
            )}
          </div>
        </div>

        {/* Info */}
        <div className="space-y-4">
          <h1 className="text-3xl font-bold">{product.title}</h1>
          <p className="text-sm text-gray-600">
            {AGE_CATEGORY_LABELS[product.age_category]}
          </p>

          <div className="flex gap-3 items-center">
            <span className="text-3xl font-bold">
              â‚¹ {parseFloat(product.price).toFixed(0)}
            </span>
            {product.mrp && (
              <span className="line-through text-gray-400">
                â‚¹ {parseFloat(product.mrp).toFixed(0)}
              </span>
            )}
          </div>

          {/* Size */}
          {product.available_sizes?.length > 0 && (
            <div>
              <p className="font-medium mb-2">Size</p>
              <div className="flex gap-2">
                {product.available_sizes.map((s) => (
                  <button
                    key={s}
                    onClick={() => setSelectedSize(s)}
                    className={`px-4 py-2 border rounded ${
                      selectedSize === s
                        ? "bg-black text-white"
                        : "bg-white"
                    }`}
                  >
                    {s}
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* Quantity */}
          <div className="flex items-center border w-fit rounded">
            <button
              onClick={() => setQuantity(Math.max(1, quantity - 1))}
              className="p-2"
            >
              <Minus />
            </button>
            <span className="px-4">{quantity}</span>
            <button
              onClick={() => setQuantity(quantity + 1)}
              className="p-2"
            >
              <Plus />
            </button>
          </div>

          {/* Buttons */}
          <button
            onClick={() => (token ? addToCart(slug as string, selectedSize) : setShowAuth(true))}
            className="w-full border-2 border-black py-3 font-semibold"
          >
            {inCart ? "Added to Cart" : "Add to Cart"}
          </button>

          <button
            onClick={() => setShowBuyNow(true)}
            className="w-full bg-black text-white py-3"
          >
            Buy Now
          </button>

          <div className="flex items-center gap-2 text-sm">
            <Truck className="w-4 h-4" />
            Estimated Delivery: {getEstimatedDelivery()}
          </div>
        </div>
      </div>

      {/* ðŸ”¥ Related Products */}
      {relatedProducts.length > 0 && (
        <div className="max-w-7xl mx-auto px-6 py-12 border-t">
          <h2 className="text-2xl font-bold mb-6">Related Products</h2>
          <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-5 gap-4">
            {relatedProducts.map((p) => (
              <Link
                key={p.id}
                href={`/products/${p.slug}`}
                className="bg-white rounded-lg shadow hover:shadow-lg"
              >
                <img
                  src={p.image1}
                  className="aspect-square object-cover rounded-t-lg"
                />
                <div className="p-3">
                  <p className="text-sm font-medium line-clamp-2">
                    {p.title}
                  </p>
                  <p className="font-bold mt-1">
                    â‚¹ {parseFloat(p.price).toFixed(0)}
                  </p>
                </div>
              </Link>
            ))}
          </div>
        </div>
      )}

      {/* Modals */}
      <BuyNowModal
        open={showBuyNow}
        onClose={() => setShowBuyNow(false)}
        product={product}
        size={selectedSize}
        onRequireAuth={() => {
          setShowBuyNow(false);
          setShowAuth(true);
        }}
      />

      <AuthModal open={showAuth} onClose={() => setShowAuth(false)} />
    </div>
  );
}







